import {Bread} from 'Bread'
import React, {PureComponent} from 'react'
import {connect} from 'react-redux'
import {mapDispatchToProps} from './redux/mapDispatchToProps'
import {Loading} from 'HOC'
import {Input, Select, message} from 'antd'
import styles from './components/style/index.scss'
import modal from './redux/modal'
const Option = Select.Option

@connect(
  (state) => ({...state.orderTime, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor() {
    super()
  }
  componentDidMount() {
    const {handleGetList} = this.props
    handleGetList()
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList()
    }
  }
  // 改变当前的订单时效
  onInputChange(values) {
    console.log(values)
    const {handleAddOrEdit} = this.props
    handleAddOrEdit(values, () => {
      message.success('修改成功')
      const {handleGetList} = this.props
      handleGetList()
    })
  }
  componentWillUnmount() {
    const {update} = this.props
    update(modal)
  }
  render() {
    const {modalTuttonLoading, selectData, searchResult} = this.props
    let selectMeun = selectData.map((o, i) => (
      <Option value={o} key={o}>
        {o}
      </Option>
    ))
    return (
      <section>
        <Bread route={['系统设置', '订单时效']} />
        <div className={styles.orderTimeBox}>
          <label>【收货中】入库单超时关单时效：</label>
          <Input
            defaultValue={searchResult.RECEIVE && searchResult.RECEIVE.sysValue}
            style={{width: 50}}
            onBlur={(e) => {
              this.onInputChange({
                data: {...searchResult.RECEIVE, sysValue: e.target.value}
              })
            }}
          />
          <span> 小时</span>
        </div>
        <div className={styles.orderTimeBox}>
          <label>【待收货】入库单超时关单时效：</label>
          <Input
            defaultValue={searchResult.WAIT && searchResult.WAIT.sysValue}
            style={{width: 50}}
            onBlur={(e) => {
              this.onInputChange({
                data: {...searchResult.WAIT, sysValue: e.target.value}
              })
            }}
          />
          <span> 小时</span>
        </div>
        <div className={styles.orderTimeBox}>
          <label>【初始】出库单超时关单时效：</label>
          <Input
            defaultValue={searchResult.INIT && searchResult.INIT.sysValue}
            style={{width: 50}}
            onBlur={(e) => {
              this.onInputChange({
                data: {...searchResult.INIT, sysValue: e.target.value}
              })
            }}
          />
          <span> 小时</span>
        </div>
        <div className={styles.orderTimeBox}>
          <label> 定时任务执行时间：</label>
          <Select
            value={searchResult.TIMING && searchResult.TIMING.sysValue}
            style={{width: 50}}
            onSelect={(value, e) => {
              this.onInputChange({
                data: {...searchResult.TIMING, sysValue: value}
              })
            }}
          >
            {selectMeun}
          </Select>
          <span> 点执行</span>
        </div>
        <Loading pastDelay={modalTuttonLoading} />
      </section>
    )
  }
}



