import * as actionType from './actionType'
import {getData} from 'util'
import {API_SYSTEMCONFIGS_ORDEREFFECT_PAGE, API_SYSTEMCONFIGS_ORDEREFFECT_UPDATE} from 'api'
//同步
export const update = (object) => ({
  type: actionType.ORDERTIME_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.ORDERTIME_UPDATE, data: {tableLoading: true}})
  getData({api: API_SYSTEMCONFIGS_ORDEREFFECT_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断

    let resData = {}
    data.result.map((o, i) => {
      resData[o.sysType] = o
    })
    dispatch({
      type: actionType.ORDERTIME_UPDATE,
      data: {
        tableLoading: false,
        searchResult: resData
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.ORDERTIME_FILE
    })
  })
}

// 编辑订单时效
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.ORDERTIME_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_SYSTEMCONFIGS_ORDEREFFECT_UPDATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.ORDERTIME_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.ORDERTIME_FILE
    })
  })
}


